import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';

import { apiFetchGet } from '../../helpers/helper';
import { addFormAction } from '../../store/form/actions';
import { formSelector } from '../../store/form/selectors';

import {
  CRow,
  CCol
} from '@coreui/react';

import Spinner from '../../components/Spinner/Spinner';
import CardCategorie from '../../components/CardCategorie/CardCategorie';

const HomeComponent = () => {
  let { numero } = useParams();
  const dispatch = useDispatch();

  const form = useSelector(formSelector);
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (numero) filters();
  }, [numero]);

  async function filters() {
    setLoading(true);
    const response = await apiFetchGet(`empresasVisible/filtro?search=${numero}`);

    if (response.data.length > 0) {
      localStorage.setItem('id_empresa', response.data[0].id);
      localStorage.setItem('razao_social', response.data[0].nome_fantasia);
      localStorage.setItem('info_boot', response.data[0].acessos_empresa.length > 0 ? JSON.stringify(response.data[0].acessos_empresa[0]) : null);
    };

    const responseCategories = await apiFetchGet(`categoriasVisible/cardapios/${response.data[0].id}?ativo=true`);
    setCategories(responseCategories.data);
    setLoading(false);

    dispatch(addFormAction({
      ...form,
      empresa: response.data[0],
      tipo_telefone: 'CELULAR'
    }));
  };

  return (
    <CRow>
      {
        loading &&
        <CCol xs="12" lg="12">
          <Spinner />
        </CCol>
      }
      {
        categories.map((categorie, index) => (
          <CCol xs="12" lg="4" key={index}>
            <CardCategorie {...{ categorie }} />
          </CCol>
        ))
      }
    </CRow>
  );
};

export default HomeComponent;
