import React from 'react';
import { useHistory } from 'react-router-dom';

import {
  CCard,
  CCardBody,
  CRow,
  CCol
} from '@coreui/react';

import Label from './LabelActive';

import defaultImg from '../../assets/image/product_blanck.jpg';

const CategorieComponent = ({ categorie }) => {
  const history = useHistory();
  const { descricao, ativo } = categorie;

  const palaraChave = disponivel => disponivel ? 'Disponível' : 'Indisponível';

  function toProducts() {
    history.push(`/products/${categorie.id}/${categorie.id_empresa}`);
  };

  return (
    <CCard
      className="custom-card-product"
      onClick={() => toProducts()}
    >
      <img
        class="card-img-top"
        src={
          categorie.categoria_imagem ?
          `http://powerindicaassociadoapi.kinghost.net:21249/delivery${categorie.categoria_imagem.url}`
          :
          defaultImg
        }
        alt="image categorie"
        style={{
          maxHeight: '300px',
          maxWidth: '100%'
        }}
      />
      <CCardBody>
        <CRow>
          <CCol xs="12" lg="12" style={{ marginBottom: '10px' }}>
            <text style={{ fontSize: '16px', fontWeight: 'bold' }}>{descricao}</text>
          </CCol>
          <CCol xs="6" lg="6">
            <Label active={ativo} label={palaraChave(ativo)} />
          </CCol>
        </CRow>
      </CCardBody>
    </CCard>
  );
};

export default CategorieComponent;
